var render = function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"about"},[_c('v-layout',[_c('v-flex',{attrs:{"xs12":"","md6":""}},[_c('h1',[_vm._v("Varför de 6 drivande behoven?")]),_c('p',[_vm._v("De 6 drivande behoven i detta sammanhang syftar till att du skall få en konkret översikt för mänsklig motivation, ett kompendium av effektiva strategier för att främja både enskilda och gruppers förändring.\n    "),_c('br'),_vm._v(" Målet med att du lära dig själva psykologin bakom de 6 drivande behoven, en metod med innovativa strategier och på så sätt utveckla färdigheter till effektiv och hållbar personlig utveckling.\n    "),_c('br'),_vm._v(" Detta är ett koncept med övertygelsen att nyckeln till både förståelse och förändringen till det mänskliga beteendet ligger i vår förmåga att förstå och hantera de 6 drivande behoven - den drivande kraften bakom mänskligt beteende.\n    "),_c('br'),_vm._v(" Varje människa – oavsett kulltur, religion eller uppväxt - har samma grundläggande behov. Oavsett om man vill förstå hur en individ, par, eller hur en grupp fungerar så ger dessa 6 behoven en praktisk ”nyckel” för att identifiera vad som driver en person, vad som hindrar en att uppleva glädje eller prestera optimalt, även vilka strategier som är mest effektiva för att främja till önskade förändringar. \n    "),_c('br'),_vm._v(" Detta är grundläggande behov, inte bara önskningar, detta är bakomliggande behov som är själva ”bränslet” till våra beteenden och som motiverar varje känsla, varje beslut och handling vi företar oss.\n    ")]),_c('br'),_c('h2',[_vm._v("Vilka är då dessa 6 mänskliga behov som styr och motiverar våra liv på detta sätt?")]),_c('br'),_c('h3',[_vm._v("1. TRYGGHET/SÄKER")]),_c('p',[_vm._v("Att uppnå känslan av att känna trygghet - att på ett eller annat sätt ”känna” sig säker är ett behov som ligger oss alla nära till hands och som vi på ett eller annat sätt strävar att uppnå. Behovet förblir detsamma men 