ull, disciplinär, perfektionist m.m \n    "),_c('br'),_vm._v(" I vår strävan efter betydelsefull är vi involverade i en slags hierarki där det är frågan om överlägsenhet eller underlägsenhet. Vi kan också känna oss betydelsefulla när vi har presterat och uppnått något, eller så kan vi uppnå behovet betydelsefullhet på mer destruktiva sätt, genom att riva ner något eller kränka någon.\n    "),_c('br'),_vm._v(" Om vi är alltför fokuserade på att vara betydelsefulla finns risken att vi får problem med att knyta an till andra eftersom vi då fokuserar för mycket på skillnaderna snarare än likheter. \n    "),_c('br'),_vm._v(" Vissa människor uppnår en känsla av betydelsefull genom att misslyckas så mycket som är möjligt, genom att vara den värsta på något. Oavsett graden på på sitt behov av att känna sig betydelsefull är det nödvändigt att alla människor får uppleva sig behövda. \n    "),_c('br'),_vm._v(" Att uppnå behovet betydelsefull/viktig kan uppnås på två sätt, antingen konstruktivt (positivt) eller destruktivt (på sätt som leder till negativa konsekvenser). Detta handlar alltså om vilka strategier man använder sig av för att uppnå behovet, såväl medvetet som omedvetet.\n    ")]),_c('h3',[_vm._v("4. KÄRLEK/SAMBAND")]),_c('p',[_vm._v("Det fjärde behovet är att uppleva känslan av kärlek, uppleva samband - att vi tillhör någon eller några. Vi alla behöver samband med andra människor, och alla strävar efter och hoppas på kärleken. \n    "),_c('br'),_vm._v(" Ett spädbarn behöver bli älskad, känna samband, få omsorg och vårdas under en längre tid om den ska utvecklas ett sunt sätt. \n    "),_c('br'),_vm._v(" Detta behov av samband och kärlek fortsätter under hela vårt liv och visar generellt hur stort och viktigt behovet är för vår överlevnad. \n    "),_c('br'),_vm._v(" Vissa människor upplever sällan kärlek, men de har många sätt att känna samband, att de tillhör andra - i samhället, eller på arbetsplatsen. Behovet av att bli älskad och känna samband är ett behov som är 